import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import BeneficiaireForm from './BeneficiaireForm';
import { fetchBeneficiaire, updateBeneficiaire, addBeneficiaire } from '../../api/beneficiairesApi';

function EditBeneficiaire() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    nom: '',
    prenom: '',
    numero: '',
    telephone: '',
    email: '',
    dateNaissance: '',
    ville: '',
    adresse: ''
  });
  const [errorMsg, setErrorMsg] = useState('');
  const [successMsg, setSuccessMsg] = useState('');

  useEffect(() => {
    if (id) {
      // Fetch beneficiary data by ID for editing
      fetchBeneficiaire(id)
        .then((data) => {
          console.log('Donnes reues du backend pour dition :', data);
          setFormData({
            ...data,
            dateNaissance: data.date_naissance ? new Date(data.date_naissance).toISOString().slice(0, 10) : '',
          });
        })
        .catch((error) => console.error('Erreur lors de la rcupration des donnes :', error));
    }
  }, [id]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setErrorMsg('');
    setSuccessMsg('');
    // Nettoyage strict : on ne garde que les champs attendus ct backend
    const dataToSend = {
      nom: formData.nom || '',
      prenom: formData.prenom || '',
      numero: formData.numero || '',
      telephone: formData.telephone || '',
      email: formData.email || '',
      dateNaissance: '', // camelCase pour le backend
      ville: formData.ville || '',
      adresse: formData.adresse || ''
    };
    if (formData.dateNaissance) {
      // On envoie la date au format ISO complet (YYYY-MM-DDTHH:mm:ss.sssZ)
      const d = new Date(formData.dateNaissance);
      if (!isNaN(d.getTime())) {
        dataToSend.dateNaissance = d.toISOString();
      } else {
        dataToSend.dateNaissance = formData.dateNaissance;
      }
      console.log('Format dateNaissance envoy au backend:', dataToSend.dateNaissance);
    }
    console.log('handleSubmit - Donnes envoyes au backend:', dataToSend); // LOG DEBUG
    const submitPromise = id
      ? updateBeneficiaire(id, dataToSend)
      : addBeneficiaire(dataToSend);
    submitPromise
      .then(() => {
        setSuccessMsg(id ? 'Bnficiaire mis  jour avec succs !' : 'Bnficiaire cr avec succs !');
        setTimeout(() => {
          navigate('/beneficiaires');
        }, 1200);
      })
      .catch((error) => {
        let msg = 'Erreur lors de la soumission du formulaire.';
        if (error && error.message) msg = error.message;
        setErrorMsg(msg);
        window.scrollTo({ top: 0, behavior: 'smooth' });
      });
  };

  return (
    <div className="page-centered-container">
      <h1 className="profile-title">
        <i className="fa fa-address-book"></i>
        {id ? 'Modifier un bnficiaire' : 'Crer un nouveau bnficiaire'}
      </h1>
      {errorMsg && (
        <div className="notification error" style={{marginBottom:16}}>
          <i className="fa fa-exclamation-circle"></i> {errorMsg}
        </div>
      )}
      <BeneficiaireForm
        formData={formData}
        handleChange={handleChange}
        handleSubmit={handleSubmit}
        id={id}
      />
      {successMsg && (
        <div className="notification success" style={{marginTop:16, marginBottom:16}}>
          <i className="fa fa-check-circle"></i> {successMsg}
        </div>
      )}
    </div>
  );
}

export default EditBeneficiaire;
