import React from 'react';
import { Navigate } from 'react-router-dom';

function RequireAuth({ children, requiredRole }) {
  const token = localStorage.getItem('token');

  // Redirige vers /login pour les utilisateurs non connects
  if (!token) {
    console.warn('Utilisateur non connect, redirection vers /login');
    return <Navigate to="/login" replace />;
  }

  // Allow mock-token for development purposes
  if (token === 'mock-token') {
    console.warn('Token fictif dtect, validation ignore pour le dveloppement.');
    return children;
  }

  try {
    const parts = token.split('.');
    if (parts.length !== 3) {
      console.error('Token mal form :', token);
      return <Navigate to="/login" replace />;
    }

    const payload = JSON.parse(atob(parts[1]));
    const isExpired = payload.exp * 1000 < Date.now();
    if (isExpired) {
      console.warn('Token expir, redirection vers /login');
      return <Navigate to="/login" replace />;
    }
    // Vrifie le rle si ncessaire
    if (requiredRole && payload.role !== requiredRole) {
      console.warn(`Accs refus : rle requis = ${requiredRole}, rle utilisateur = ${payload.role}`);
      return <Navigate to="/access-denied" replace />;
    }
  } catch (error) {
    console.error('Erreur lors de la validation du token :', error);
    return <Navigate to="/login" replace />;
  }

  return children;
}

export default RequireAuth;
