import React, { useState, useEffect, useContext } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import UserForm from './UserForm';
import { AuthContext } from '../../context/AuthContext';

function EditUser() {
  const { id } = useParams();
  const navigate = useNavigate();
  const { userId } = useContext(AuthContext); // Rcupre l'ID de l'utilisateur connect
  const [user, setUser] = useState({
    nom: '',
    prenom: '',
    email: '',
    username: '',
    password: '',
    role: ''
  });
  const [successMsg, setSuccessMsg] = useState('');

  useEffect(() => {
    const fetchUserId = id || userId; // Utilise l'ID de l'utilisateur connect si aucun ID n'est fourni
    console.log('Fetching user with ID:', fetchUserId); // Vrifie l'ID utilis

    // Utilise l'URL de l'API en production si disponible
    const apiUrl = process.env.REACT_APP_API_URL || '';
    const fullUrl = `${apiUrl}/api/users/${fetchUserId}`;
    console.log('API URL utilise:', fullUrl);
    fetch(fullUrl)
      .then((response) => {
        console.log('Response status:', response.status); // Vrifie le statut de la rponse
        if (!response.ok) {
          throw new Error(`Erreur HTTP: ${response.status}`);
        }
        return response.json();
      })
      .then((data) => {
        // Fusionne les donnes reues avec l'tat initial pour garantir tous les champs
        setUser(prev => ({ ...prev, ...data }));
      })
      .catch((error) => {
        console.error('Erreur lors de la rcupration de l\'utilisateur :', error);
        alert('Impossible de rcuprer les donnes de l\'utilisateur. Veuillez ressayer plus tard.');
      });
  }, [id, userId]);

  const handleSubmit = (e) => {
    e.preventDefault();
    const method = id ? 'PUT' : 'POST';
    const apiUrl = process.env.REACT_APP_API_URL || '';
    const url = id ? `${apiUrl}/api/users/${id}` : `${apiUrl}/api/users`;

    fetch(url, {
      method,
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(user),
    })
      .then((response) => {
        if (!response.ok) throw new Error('Erreur lors de la mise  jour');
        return response.json();
      })
      .then(() => {
        setSuccessMsg('Informations utilisateur mises  jour avec succs.');
        setTimeout(() => navigate('/users'), 1200);
      })
      .catch((error) => {
        console.error('Erreur lors de l\'enregistrement de l\'utilisateur :', error);
        alert('Erreur lors de la mise  jour.');
      });
  };

  useEffect(() => {
    if (successMsg) {
      window.scrollTo({ top: document.body.scrollHeight, behavior: 'smooth' });
    }
  }, [successMsg]);

  return (
    <div className="page-centered-container">
      <h1 className="profile-title">
        <i className="fa fa-user"></i>
        {id ? 'Modifier' : 'Ajouter'} un utilisateur
      </h1>
      <form className="uni-form uni-form-container" onSubmit={handleSubmit}>
        <UserForm
          formData={user}
          handleChange={(e) => setUser({ ...user, [e.target.name]: e.target.value })}
          id={id}
        />
        <button type="submit">{id ? 'Mettre  jour' : 'Crer'}</button>
        {successMsg && (
          <div className="form-success" style={{marginTop:18, textAlign:'center'}}>
            <i className="fa fa-check-circle" style={{marginRight:6}}></i> {successMsg}
          </div>
        )}
      </form>
    </div>
  );
}

export default EditUser;
