import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './AccessDenied.css';

function AccessDenied() {
  const navigate = useNavigate();

  useEffect(() => {
    const timer = setTimeout(() => {
      navigate('/login', { replace: true });
    }, 15000);

    return () => clearTimeout(timer);
  }, [navigate]);

  const handleGoToLogin = () => {
    navigate('/login');
  };

  return (
    <div className="access-denied-container">
      <h1>Accs refus</h1>
      <p>Vous n'avez pas les autorisations ncessaires pour accder  cette page.</p>
      <p>Vous serez redirig vers la page de connexion dans 15 secondes.</p>
      <img src="/images/access-denied.webp" alt="Accs refus" />
      <button onClick={handleGoToLogin}>Retour  la page de connexion</button>
    </div>
  );
}

export default AccessDenied;
